import React from 'react';
import { Button } from '../../../shared/components/Elements';
import { Field } from 'formik';
import { Loader } from '../../../shared/components';
import EntityForm from '../../../modules/entity/forms';
import { useAppState } from '../../../shared/state';
import { useNotistack } from '../../../shared/hooks';
import { KntInputField } from '../../../shared/components/Fields';
import { MAX_STATUS_TITLE_CHARACTER } from '../constants';
import { useTranslation } from 'react-i18next';

type Props = {
	onClose: () => void;
};

function StatusCreateForm({ onClose }: Props) {
	const { companyId, selectedStatus } = useAppState();
	const { showNotification } = useNotistack();
	const { t } = useTranslation();

	return (
		<EntityForm.Main
			id={selectedStatus?.statusId}
			entity="statuses"
			name={`AllStatuses-${companyId}`}
			url={`/${companyId}/my/company/case_statuses/${selectedStatus?.statusId}/`}
			method="put"
			primaryKey="id"
			appendData
			sendAsFormData={false}
			normalizeData={(data: any) => data}
			fields={[
				{
					name: 'title',
					required: true,
					value: selectedStatus?.name || '',
					onSubmitKey: ''
				}
			]}
			onSuccess={(response, resetForm): any => {
				showNotification({
					message: t('status_updated_success'),
					variant: 'success'
				});
				onClose();
			}}
			onError={(error, resetForm): any => {
				// resetForm();
				showNotification({
					message: error?.response?.data?.message ?? t('something_went_wrong'),
					variant: 'error'
				});
			}}
		>
			{({ values, isSubmitting }) => {
				return (
					<div>
						<h2 className="mb-4 text-xl font-bold">{t('edit_status_name')}</h2>
						<Field
							label={t('status_title')}
							labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
							name="title"
							component={KntInputField}
							placeholder={t('type_status_title')}
							required
							autoFocus
							characterCount={values.title.length}
							characterLimit={MAX_STATUS_TITLE_CHARACTER}
							id="mail.com"
						/>
						<div className="mt-4 flex items-center justify-end">
							<Button variant="outlined" type="button" onClick={onClose}>
								{t('cancel')}
							</Button>
							<Button
								variant="contained"
								type="submit"
								className="!ml-4"
								color="primary"
								disabled={!values.title || isSubmitting}
							>
								{t('update')}
							</Button>
						</div>
						{isSubmitting && <Loader tiled />}
					</div>
				);
			}}
		</EntityForm.Main>
	);
}

export default StatusCreateForm;
