import React, { useEffect, useRef } from 'react';
import { Droppable } from 'react-beautiful-dnd';
import { FixedSizeList } from 'react-window';
import { StatusType } from './Status';
import InfiniteLoader from 'react-window-infinite-loader';
import { DragItem } from './DragItem';
import { DndRow } from './DndRow';
import config from 'config';
import { ToggleCaseDetailsOpen } from './case-details/CaseDetails.types';
import { useWindowSize } from '../../../shared/hooks';
import { isMobile } from 'react-device-detect';

type Props = {
	items: any;
	status: StatusType;
	isFetched: boolean;
	loadMore: (hasToLoadMore?: any) => void;
	hasNextPage: number | boolean | undefined;
	toggleCaseDetailsOpen: ToggleCaseDetailsOpen;
	resetCacheKeys: any[];
};

const HEADER_PLUS_STATUS_HEADER_PLUS_MARGINS_HEIGHTS_DESK = 144;
const HEADER_PLUS_STATUS_HEADER_PLUS_MARGINS_HEIGHTS_MOB = 120;

export const ListComponent = React.memo(function ItemList({
	items,
	status,
	isFetched,
	loadMore,
	hasNextPage,
	toggleCaseDetailsOpen,
	resetCacheKeys
}: Props) {
	const { height: windowHeight } = useWindowSize();
	// There is an issue I have noticed with react-window that when reordered
	// react-window sets the scroll back to 0 but does not update the UI
	// I should raise an issue for this.
	// As a workaround I am resetting the scroll to 0
	// on any list that changes it's index

	//TODO:REMOVE IF DRAG-N-DROP FEATURE IS DONE AND WORKING JUST FINE WITHOUT ISSUES
	// const listRef = useRef();
	// useLayoutEffect(() => {
	// 	const list = listRef.current;
	// 	if (list) {
	// 		// @ts-ignore
	// 		list.scrollTo(0);
	// 	}
	// }, [index]);

	// We create a reference for the InfiniteLoader
	const infiniteLoaderRef = useRef(null);
	const hasMountedRef = useRef(false);

	// Each time the sort prop changed we called the method resetloadMoreItemsCache to clear the cache
	useEffect(() => {
		// We only need to reset cached items when "sortOrder" changes.
		// This effect will run on mount too; there's no need to reset in that case.
		if (hasMountedRef.current) {
			if (infiniteLoaderRef.current) {
				// @ts-ignore
				infiniteLoaderRef?.current?.resetloadMoreItemsCache();
			}
		}
		hasMountedRef.current = true;
	}, [resetCacheKeys]);

	return (
		<Droppable
			droppableId={status.id.toString()}
			mode="virtual"
			type={config.CASES_DROPPABLE_TYPE}
			renderClone={(provided, snapshot, rubric) => (
				<DragItem
					provided={provided}
					isDragging={snapshot.isDragging}
					item={items[rubric.source.index]}
					isFetched={isFetched}
					toggleCaseDetailsOpen={toggleCaseDetailsOpen}
				/>
			)}
		>
			{(provided) => {
				// Add an extra item to our list to make space for a dragging item
				// Usually the DroppableProvided.placeholder does this, but that won't
				// work in a virtual list
				// const itemCount = snapshot.isUsingPlaceholder ? items.length + 1 : items.length;
				const itemCount = hasNextPage ? items.length + 1 : items.length;

				return (
					<InfiniteLoader
						isItemLoaded={(index) => index < items.length}
						itemCount={itemCount}
						loadMoreItems={loadMore}
						ref={infiniteLoaderRef}
					>
						{({ onItemsRendered, ref }) => {
							return (
								<FixedSizeList
									height={
										windowHeight
											? windowHeight -
											  (isMobile
													? HEADER_PLUS_STATUS_HEADER_PLUS_MARGINS_HEIGHTS_MOB
													: HEADER_PLUS_STATUS_HEADER_PLUS_MARGINS_HEIGHTS_DESK)
											: 0
									}
									itemCount={itemCount}
									itemSize={166}
									width={362}
									outerRef={provided.innerRef}
									itemData={{ itemsData: items, isFetched, toggleCaseDetailsOpen }}
									className="task-list pb-8"
									// @ts-ignore TODO: remove after dnd logic completed
									// ref={listRef}
									onItemsRendered={onItemsRendered}
									ref={ref}
								>
									{DndRow}
								</FixedSizeList>
							);
						}}
					</InfiniteLoader>
				);
			}}
		</Droppable>
	);
});
