import React from 'react';
import { BiDotsVerticalRounded } from 'react-icons/bi';
import { Checkbox, FormControlLabel, Menu } from 'shared/components/Elements';
import MenuItem from '@material-ui/core/MenuItem';
import { useStatusHeaderMenu } from '../hooks';
import { useAppState } from 'shared/state';
import { useTranslation } from 'react-i18next';
import { useTranslationHelpers } from 'shared/hooks/useTranslationHelpers';
import clsx from 'clsx';

type Props = {
	statusId: number;
	name: string;
	isConfirmationRequired: boolean;
};

export function StatusHeaderDots({ statusId, name, isConfirmationRequired }: Props) {
	const { open, anchorEl, handleClick, handleCloseMenu } = useStatusHeaderMenu();
	const { setSelectedStatus, isAdmin, unreadCasesCountByStatus } = useAppState();
	const { t } = useTranslation();
	const { isRTL } = useTranslationHelpers();

	return (
		<>
			<div
				className={clsx(
					' more-btn absolute top-3 h-6 w-4 cursor-pointer text-right text-kgrey-menuText transition-colors hover:text-primary dark:text-white dark:hover:text-primary',
					{ 'right-2.5': !isRTL, 'left-2.5': isRTL }
				)}
				onClick={handleClick}
			>
				<BiDotsVerticalRounded className={'inline'} />
			</div>
			{open ? (
				<Menu id="long-menu" anchorEl={anchorEl} keepMounted open={open} onClose={handleCloseMenu}>
					{isAdmin ? (
						<MenuItem
							onClick={() => {
								handleCloseMenu();
								setSelectedStatus({ name, statusId, type: 'edit' });
							}}
						>
							{t('edit_status_name')}
						</MenuItem>
					) : null}

					{isAdmin ? (
						<MenuItem
							onClick={() => {
								handleCloseMenu();
								setSelectedStatus({ name, statusId, type: 'delete' });
							}}
						>
							{t('delete_status')}
						</MenuItem>
					) : null}
					{unreadCasesCountByStatus[statusId] ? (
						<MenuItem
							onClick={() => {
								handleCloseMenu();
								setSelectedStatus({ name, statusId, type: 'mark_all_read' });
							}}
						>
							{t('mark_all_read')}
						</MenuItem>
					) : null}
					<MenuItem
						onClick={() => {
							handleCloseMenu();
							setSelectedStatus({ name, statusId, type: 'add_buttons' });
						}}
					>
						<FormControlLabel
							control={
								<Checkbox color="primary" checked={isConfirmationRequired} className="!py-0" />
							}
							label={t('add_buttons')}
							labelPlacement="start"
							className="!ml-0"
						/>
					</MenuItem>
				</Menu>
			) : null}
		</>
	);
}
