import React from 'react';
import { Button } from '../../../shared/components/Elements';
import { Field } from 'formik';
import { Loader } from '../../../shared/components';
import EntityForm from '../../../modules/entity/forms';
import { useAppState } from '../../../shared/state';
import { useNotistack } from '../../../shared/hooks';
import { KntInputField } from '../../../shared/components/Fields';
import { useSelector } from 'react-redux';
import { IRootState } from '../../../shared/interfaces';
import config from '../../../config';
import { MAX_STATUS_TITLE_CHARACTER } from '../constants';
import { useTranslation } from 'react-i18next';

type Props = {
	onClose: () => void;
};

function StatusCreateForm({ onClose }: Props) {
	const { t } = useTranslation();
	const { companyId, caseType } = useAppState();
	const { showNotification } = useNotistack();
	const entityObjectInReduxState = useSelector((state: IRootState) => state.entity);

	const entityName = `AllStatuses-${companyId}`;
	const statusIds = entityObjectInReduxState.statuses?.[entityName].ids ?? [];

	return (
		<EntityForm.Main
			entity="statuses"
			name={`AllStatuses-${companyId}`}
			url={`/${companyId}/my/company/case_statuses/`}
			method="post"
			primaryKey="id"
			sendAsFormData={false}
			normalizeData={(data: any) => data}
			appendData
			fields={[
				{
					name: 'title',
					required: true,
					onSubmitKey: ''
				},
				{
					name: 'order_number',
					value: statusIds.length,
					onSubmitKey: ''
				},
				{
					name: 'is_default',
					value: false,
					onSubmitKey: ''
				},
				{
					name: 'is_staff',
					value: caseType === config.STAFF,
					onSubmitKey: ''
				},
				{
					name: 'is_feedback_required',
					value: false,
					onSubmitKey: ''
				},
				{
					name: 'is_confirmation_required',
					value: false,
					onSubmitKey: ''
				}
			]}
			onSuccess={(response, resetForm): any => {
				showNotification({
					message: t('status_created_success'),
					variant: 'success'
				});
				onClose();
			}}
			onError={(error, resetForm): any => {
				// resetForm();
				showNotification({
					message: error?.response?.data?.message ?? t('something_went_wrong'),
					variant: 'error'
				});
			}}
		>
			{({ values, isSubmitting }) => {
				return (
					<div>
						<h2 className="mb-4 text-xl font-bold">{t('create_new_status')}</h2>
						<Field
							label={t('status_title')}
							labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
							name="title"
							component={KntInputField}
							placeholder={t('type_status_title')}
							required
							characterLimit={MAX_STATUS_TITLE_CHARACTER}
							characterCount={values.title.length}
						/>
						<div className="mt-4 flex items-center justify-end">
							<Button variant="outlined" type="button" onClick={onClose}>
								{t('cancel')}
							</Button>
							<Button
								variant="contained"
								type="submit"
								className="!ml-4"
								color="primary"
								disabled={!values.title || isSubmitting}
							>
								{t('create')}
							</Button>
						</div>
						{isSubmitting && <Loader tiled />}
					</div>
				);
			}}
		</EntityForm.Main>
	);
}

export default StatusCreateForm;
