import React from 'react';
import { useAppState } from 'shared/state';
import { CaseItemMarkup } from './CaseItemMarkup';
import { SearchCaseEntity } from 'shared/interfaces';
import { ErrorBoundary } from 'shared/components';
import { ToggleCaseDetailsOpen } from './case-details/CaseDetails.types';

export type SearchedCaseType = {
	id: number;
	text: any;
	entity_id: number;
	recurring_id: number;
	pin_time: null | string;
	case_: SearchCaseEntity;
	from_user: {
		first_name: string;
		last_name: string;
	};
};

type Props = {
	caseData: SearchedCaseType;
	isDragging?: boolean;
	toggleCaseDetailsOpen: ToggleCaseDetailsOpen;
};

export const SearchCaseItemInner = ({ caseData, isDragging, toggleCaseDetailsOpen }: Props) => {
	const { unreadMessagesCount } = useAppState();

	const department = caseData.case_?.department,
		sub_department = caseData.case_?.sub_department,
		caseTitle = caseData?.case_?.pinned_message?.text,
		last_message_type = caseData.case_?.last_message_type,
		last_message_text = caseData.text,
		last_message_author = caseData.from_user,
		case_number = caseData.case_?.case_number,
		priority = caseData.case_?.priority,
		last_message_time = caseData.case_?.last_message_time,
		caseId = caseData?.entity_id,
		statusId = caseData?.case_.status,
		draftMessage = caseData?.case_.draft_message,
		assigneeIds = caseData?.case_.assignee_ids,
		hasLinkedCase = caseData?.case_.has_linked_case,
		lastReadMessage = caseData?.case_.last_read_message,
		recurringId = caseData?.recurring_id;

	const hasUnread = unreadMessagesCount?.[caseId];

	return (
		<CaseItemMarkup
			case_number={case_number}
			caseTitle={caseTitle}
			department={department}
			sub_department={sub_department}
			last_message_author={last_message_author}
			last_message_time={last_message_time}
			last_message_type={last_message_type}
			last_message_text={last_message_text}
			hasUnread={hasUnread}
			caseId={caseId}
			pin_time={caseData?.pin_time}
			statusId={statusId}
			searchedMessageId={caseData.id}
			priority={priority}
			draftMessage={draftMessage}
			assigneeIds={assigneeIds}
			isDragging={isDragging}
			toggleCaseDetailsOpen={toggleCaseDetailsOpen}
			recurringId={recurringId}
			relatedTo={null}
			hasLinkedCase={hasLinkedCase}
			lastReadMessage={lastReadMessage}
		/>
	);
};

export function SearchCaseItem(props: Props) {
	return (
		<ErrorBoundary isCompact>
			<SearchCaseItemInner {...props} />
		</ErrorBoundary>
	);
}
