import React, { useState } from 'react';
import { KntTwilioStyle } from '../../../shared/services';
import { AsyncSelect, Loader } from '../../../shared/components';
import { StatusType } from './Status';
import { useAppState } from '../../../shared/state';
import { Button } from '../../../shared/components/Elements';
import { useCaseController } from '../hooks/useCaseController';
import { useTranslation } from 'react-i18next';

export function StatusChangeMobile() {
	const { isStaff, statusChangePayload, companyId, setStatusChangePayload } = useAppState();
	const [loading, setLoading] = useState(false);
	const { changeCaseStatus } = useCaseController();
	const [nextStatus, setNextStatus] = useState<null | StatusType>(null);
	const { t } = useTranslation();

	const onSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
		e.preventDefault();
		setLoading(true);

		try {
			await changeCaseStatus({
				prevStatusId: statusChangePayload?.prevStatus as number,
				nextStatusId: nextStatus?.id as number,
				isStaffCase: isStaff,
				sourceIndex: statusChangePayload?.sourceIndex,
				destinationIndex: 0,
				entityId: Number(statusChangePayload?.entityId),
				searchEntityId: statusChangePayload?.searchEntityId,
				isDragging: false
			});
			setLoading(false);
			closeStatusChangePopup();
		} catch (e) {
			setLoading(false);
		}
	};

	const closeStatusChangePopup = () => setStatusChangePayload(null);

	return (
		<form onSubmit={onSubmit}>
			<h2 className="mb-4 text-xl font-bold">Status Change</h2>

			{/*@ts-ignore*/}
			<AsyncSelect
				customStylesProps={KntTwilioStyle}
				name="status_change"
				placeholder="Select Status"
				optionValue="id"
				optionLabel="title"
				loadOptionsKey=""
				isSearchable
				isClearable
				alwaysRefetch
				value={nextStatus}
				// customStyles="KntTwilioStyle"
				loadOptionsOnMenuOpen
				reverse={true}
				loadOptionsUrl={`/${companyId}/my/company/case_statuses/`}
				loadOptionsParams={(search: string) => ({
					extra: {
						search,
						is_staff: !isStaff ? 'false' : 'true'
					}
				})}
				isOptionDisabled={(option: StatusType) => option.id === statusChangePayload?.prevStatus}
				onChange={(option: StatusType) => setNextStatus(option)}
			/>

			<div className="mt-4 flex items-center justify-end">
				<Button variant="outlined" type="button" onClick={closeStatusChangePopup}>
					{t('cancel')}
				</Button>
				<Button
					variant="contained"
					type="submit"
					className="!ml-4"
					color="primary"
					disabled={loading || !nextStatus}
				>
					Change
				</Button>
			</div>
			{loading && <Loader tiled />}
		</form>
	);
}
