import React from 'react';
import { Draggable } from 'react-beautiful-dnd';
import { DragItem } from './DragItem';
import { areEqual } from 'react-window';
import { SearchedCaseType } from './SearchCaseItem';
import { CaseType } from './CaseItem';

type RowProps = {
	data: any;
	index: number;
	style: Record<string, any>;
};
type CaseDragItem = SearchedCaseType | CaseType;
const getDragId = (item: CaseDragItem) => {
	if ('case_' in item) {
		return `${item?.id?.toString()}|${item?.case_?.id?.toString()}`;
	} else {
		return `${item?.id?.toString()}|${item?.id?.toString()}`;
	}
};

// Recommended react-window performance optimisation: memoize the row render function
// Things are still pretty fast without this, but I am a sucker for making things faster
export const DndRow = React.memo(function Row(props: RowProps) {
	const { data: items, index, style } = props;
	const item = items.itemsData[index];
	const toggleCaseDetailsOpen = items.toggleCaseDetailsOpen;

	// We are rendering an extra item for the placeholder
	if (!item) {
		return null;
	}

	const draggableId = getDragId(item);

	return (
		<Draggable draggableId={draggableId} index={index} key={draggableId}>
			{(provided, snapshot) => (
				<DragItem
					isFetched={items.isFetched}
					provided={provided}
					item={item}
					style={style}
					isDragging={snapshot.isDragging}
					toggleCaseDetailsOpen={toggleCaseDetailsOpen}
				/>
			)}
		</Draggable>
	);
}, areEqual);
