import React, { useState } from 'react';
import { Button } from 'shared/components';
import StatusCreateForm from './StatusCreateForm';
import { WhiteModal } from '../../../shared/components/Elements';
import { useTranslation } from 'react-i18next';

export function StatusCreateButton() {
	const [visibleForm, setVisibleForm] = useState(false);
	const { t } = useTranslation();

	const toggleForm = () => setVisibleForm((prev) => !prev);

	return (
		<div className="status mr-4 flex h-full w-[362px] flex-col">
			<Button variant="outlined" color="primary" onClickHandler={toggleForm}>
				{t('create_status')}
			</Button>
			<WhiteModal handleModalOpen={toggleForm} open={visibleForm} maxWidth={446}>
				<StatusCreateForm onClose={toggleForm} />
			</WhiteModal>
		</div>
	);
}
