/** @jsxImportSource @emotion/react */
import React, { useEffect } from 'react';
import { useAppState } from 'shared/state';
import { Status, StatusType } from './components/Status';
import useDimensions from 'react-use-dimensions';
import { css } from '@emotion/react';
import EntityContainer from 'modules/entity/containers';
import { StatusLoader } from 'shared/components/Loaders';
import Grow from '@material-ui/core/Grow';
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import DayJsUtils from '@date-io/dayjs';
import { useUnreadCaseTab, useUnreadCount } from 'shared/hooks';
import sortBy from 'lodash/sortBy';
import { Helmet } from 'react-helmet';
import { DragDropContext, Droppable } from 'react-beautiful-dnd';
import { ConfirmModal, ErrorBoundary } from 'shared/components';
import config from 'config';
import { useCaseController } from './hooks/useCaseController';
import { storage } from '../../shared/services';
import { isMobile } from 'react-device-detect';
import { ConfirmationStatusesForm, StatusCreateButton } from './components';
import { WhiteModal } from '../../shared/components/Elements';
import StatusEditForm from './components/StatusEditForm';
import { useCaseLoadingLogics, useScrollToMostLeftUnread, useStatusHeaderMenu } from './hooks';
import { StatusChangeMobile } from './components/StatusChangeMobile';
import { StatusPriorityMessagePopup } from '../../shared/components/Chat/components';
import clsx from 'clsx';
import { useSelector } from 'react-redux';
import { IRootState } from '../../shared/interfaces';
import { isThereAnySearchOrFilterMatchingCase } from './helpers';
import { useTranslation } from 'react-i18next';
import { useTranslationHelpers } from 'shared/hooks/useTranslationHelpers';

function CasesInner() {
	const {
		setPageType,
		caseType,
		selectedCaseFilter,
		setSelectedStatus,
		selectedStatus,
		statusChangePayload,
		setStatusChangePayload,
		isPermitted,
		searchQuery
	} = useAppState();
	const [casePageRef, { height: casePageHeight }, node] = useDimensions();
	const {
		companyId,
		setSearchQuery,
		toggleChatPanel,
		statusPriorityMessagePopup,
		setStatusPriorityMessagePopup,
		isDarkMode
	} = useAppState();
	const { isRTL } = useTranslationHelpers();
	const { unreadCasesQuantity, isHidden } = useUnreadCaseTab();
	const { onStatusDragEnd } = useCaseController();
	const { fetchCompanyAllUnreadCounts } = useUnreadCount();
	const { deleteStatusHandler, statusDeleteLoader, markAllReadHandler, markAllReadLoader } =
		useStatusHeaderMenu();
	const { t } = useTranslation();

	const cases = useSelector((state: IRootState) => state?.entity?.cases);

	//Cases loading general logics
	useCaseLoadingLogics();

	//Scroll to Left Most Unread
	useScrollToMostLeftUnread();

	const parsedSelectedCaseFromLS = JSON.parse(storage.get('selectedCaseFilter') || '{}')[caseType];
	const selectedFilterTemplate = selectedCaseFilter[caseType] || parsedSelectedCaseFromLS;

	useEffect(() => {
		setPageType(true);
		return () => {
			setPageType(false);
		};
	}, [setPageType]);

	//Reset search When we leave Case Messages page
	useEffect(() => {
		return () => {
			setSearchQuery('');
			toggleChatPanel(false);
		};
	}, []);

	//Load Status unread cases count
	useEffect(() => {
		if (companyId) {
			(async () => {
				await fetchCompanyAllUnreadCounts();
			})();
		}
	}, [companyId, caseType]);

	const closeEditStatusModal = () => setSelectedStatus(null);
	const closeMobileStatusChangePopup = () => setStatusChangePayload(null);
	const closeStatusPriorityMessagePopup = () => setStatusPriorityMessagePopup(null);

	return (
		<MuiPickersUtilsProvider utils={DayJsUtils}>
			<div
				ref={casePageRef}
				className={clsx('cases-page-wrapper flex h-full w-full flex-col pb-1 pt-2  lg:pt-5', {
					'cases-page-wrapper-dark': isDarkMode,
					'pl-2 lg:pl-7': !isRTL,
					'pr-2 lg:pr-7': isRTL
				})}
			>
				{(!isHidden || unreadCasesQuantity === 0) && (
					<Helmet>
						<title>KENNEKT | Cases</title>
					</Helmet>
				)}
				{companyId ? (
					<EntityContainer.All
						entity={'statuses'}
						name={`AllStatuses-${companyId}`}
						url={`/${companyId}/my/company/case_statuses/`}
						reverse={true}
						params={{
							extra: {
								is_staff: caseType === config.STAFF ? 'true' : 'false'
							}
						}}
						dataKey={(data: any) => data}
					>
						{({ items, isFetched, meta }) => {
							const statuses = items.map((item: any) => item) as StatusType[];

							const isMatchingFilters = isThereAnySearchOrFilterMatchingCase({
								statuses,
								cases,
								caseType,
								searchQuery,
								selectedFilterTemplate
							});

							return (
								<DragDropContext onDragEnd={onStatusDragEnd}>
									<div
										className={
											'case-statuses-scroll-container relative flex h-full flex-grow overflow-hidden overflow-x-auto bg-white dark:bg-kdark'
										}
										css={css`
											height: ${casePageHeight - (isMobile ? 8 : 20)}px;
										`}
									>
										<Droppable
											droppableId={config.STATUS_DROPPABLE_ID}
											direction="horizontal"
											type={config.STATUS_DROPPABLE_TYPE}
										>
											{(provided) => {
												return (
													<div className="flex">
														<div
															className="statuses-wrapper flex"
															{...provided.droppableProps}
															ref={provided.innerRef}
														>
															<>
																{(selectedFilterTemplate?.filter_json?.status?.length
																	? sortBy(
																			selectedFilterTemplate?.filter_json?.status,
																			'order_number'
																	  )
																	: statuses
																).map((item: StatusType, index) => {
																	return (
																		<Grow
																			in={true}
																			key={item.id || index}
																			{...{ timeout: 500 * (index + 1) }}
																		>
																			<Status status={item} key={item.id} index={index} />
																		</Grow>
																	);
																})}
																{provided.placeholder}
															</>
															{/* Status Create Form */}
															{isFetched &&
															isPermitted &&
															!searchQuery.length &&
															!selectedFilterTemplate ? (
																<StatusCreateButton />
															) : null}
														</div>
													</div>
												);
											}}
										</Droppable>
										{/*Show Statuses loading skeletons*/}
										{!isFetched && (
											<>
												<div className="flex">
													{[...Array(6)].map((item: any, index: number) => {
														return (
															<Grow in={true} key={index} {...{ timeout: 300 * (index + 1) }}>
																<div className={'mr-4'}>
																	<StatusLoader />
																</div>
															</Grow>
														);
													})}
												</div>
											</>
										)}
										{isFetched &&
											!isMatchingFilters &&
											(selectedFilterTemplate || searchQuery.length) ? (
												<div className="flex w-full items-center justify-center text-xl dark:text-white">
													{t('no_matching_case_found')}
												</div>
											) : null}

										{/*Edit Status Name*/}
										<WhiteModal
											handleModalOpen={closeEditStatusModal}
											open={selectedStatus?.type === 'edit' ? !!selectedStatus : false}
											maxWidth={446}
										>
											<StatusEditForm onClose={closeEditStatusModal} />
										</WhiteModal>

										{/*Delete Status*/}
										<ConfirmModal
											featureHandler={deleteStatusHandler}
											isOpen={Boolean(selectedStatus?.type === 'delete' ? !!selectedStatus : false)}
											toggleHandler={closeEditStatusModal}
											title={`Are You Sure To Delete ${selectedStatus?.name.toUpperCase()} Status?`}
											hasLoader
											isLoading={statusDeleteLoader}
										/>

										{/*Delete Status*/}
										<ConfirmModal
											featureHandler={markAllReadHandler}
											isOpen={Boolean(
												selectedStatus?.type === 'mark_all_read' ? !!selectedStatus : false
											)}
											toggleHandler={closeEditStatusModal}
											title={
												<>
													<h3 className="mb-4 text-center">{selectedStatus?.name}</h3>
													<p className="text-center">{t('mark_all_cases_as_read')}?</p>
												</>
											}
											hasLoader
											isLoading={markAllReadLoader}
										/>

										{/* Add Confirmation Buttons */}
										<WhiteModal
											handleModalOpen={closeEditStatusModal}
											open={selectedStatus?.type === 'add_buttons' ? !!selectedStatus : false}
											maxWidth={446}
										>
											<ConfirmationStatusesForm closeEditStatusModal={closeEditStatusModal} />
										</WhiteModal>

										{/* Status Change Popup for Mobile */}
										<WhiteModal
											handleModalOpen={closeMobileStatusChangePopup}
											open={!!statusChangePayload}
											maxWidth={446}
										>
											<StatusChangeMobile />
										</WhiteModal>

										{/* Priority and Status Message Popup */}
										<WhiteModal
											handleModalOpen={closeStatusPriorityMessagePopup}
											open={statusPriorityMessagePopup?.type === 'case'}
											maxWidth={446}
										>
											<StatusPriorityMessagePopup onClose={closeStatusPriorityMessagePopup} />
										</WhiteModal>
									</div>
								</DragDropContext>
							);
						}}
					</EntityContainer.All>
				) : null}
			</div>
		</MuiPickersUtilsProvider>
	);
}

export default function Cases() {
	return (
		<ErrorBoundary>
			<CasesInner />
		</ErrorBoundary>
	);
}
