import React from 'react';
import EntityForm from 'modules/entity/forms';
import { useAppState } from 'shared/state';
import AsyncSelectJs from 'shared/components/Fields/AsyncSelectJs';
import { KntTwilioStyle } from 'shared/services';
import { Field } from 'formik';
import { Button } from 'shared/components/Elements';
import { Loader } from 'shared/components';
import { useGetEntity, useNotistack } from 'shared/hooks';
import { StatusType } from './Status';
import { useTranslation } from 'react-i18next';

type Props = {
	closeEditStatusModal: () => void;
};

export function ConfirmationStatusesForm({ closeEditStatusModal }: Props) {
	const { isStaff, companyId, selectedStatus } = useAppState();
	const { showNotification } = useNotistack();
	const { t } = useTranslation();

	const { all: statuses } = useGetEntity({
		entity: 'statuses',
		entityName: `AllStatuses-${companyId}`
	});

	const currentSelectedStatus = (statuses.items as StatusType[]).find(
		(st) => st.id === selectedStatus?.statusId
	);

	return (
		<>
			<EntityForm.Main
				id={selectedStatus?.statusId}
				entity="statuses"
				name={`AllStatuses-${companyId}`}
				url={`/${companyId}/my/company/case_statuses/${selectedStatus?.statusId}/`}
				method="patch"
				primaryKey="id"
				appendData
				sendAsFormData={false}
				normalizeData={(data: any) => data}
				fields={[
					{
						name: 'confirmed',
						value: currentSelectedStatus?.confirmed,
						onSubmitValue: (value) => (value ? { id: value.id } : null),
						onSubmitKey: '',
						type: 'object'
					},
					{
						name: 'not_confirmed',
						value: currentSelectedStatus?.not_confirmed,
						onSubmitValue: (value) => (value ? { id: value.id } : null),
						onSubmitKey: '',
						type: 'object'
					},
					{
						name: 'is_confirmation_required',
						value: true,
						onSubmitValue: (value, values) => !!(values as any).not_confirmed,
						onSubmitKey: '',
						type: 'object'
					},
					{
						name: 'title',
						value: currentSelectedStatus?.title,
						onSubmitKey: '',
						type: 'object'
					}
				]}
				onSuccess={(response, resetForm): any => {
					closeEditStatusModal();
				}}
				onError={(error, resetForm): any => {
					// resetForm();
					showNotification({
						message: error?.response?.data?.message ?? 'Something went wrong',
						variant: 'error'
					});
				}}
			>
				{({ values, isSubmitting }) => {
					return (
						<div>
							<h1 className="mb-6 text-lg font-bold">{t('action_buttons')}</h1>
							<div className="react-select-dropdown mb-4">
								<Field
									component={AsyncSelectJs}
									customStylesProps={KntTwilioStyle}
									label={t('not_confirmed')}
									labelClasses="ml-2 text-sm font-light text-kgrey-menuText mb-2 block"
									name="not_confirmed"
									optionLabel="title"
									optionValue="id"
									isSearchable
									isCached
									isClearable
									placeholder={t('select_not_confirmed_status')}
									reverse={true}
									loadOptionsUrl={`/${companyId}/my/company/case_statuses/`}
									loadOptionsParams={(search: string) => ({
										extra: {
											search,
											is_staff: !isStaff ? 'false' : 'true'
										}
									})}
									isOptionDisabled={(option: StatusType) =>
										option.id === currentSelectedStatus?.id || values?.confirmed?.id === option.id
									}
									loadOptionsKey=""
								/>
							</div>
							<div className="react-select-dropdown mb-4">
								<Field
									component={AsyncSelectJs}
									customStylesProps={KntTwilioStyle}
									label={t('confirmed')}
									labelClasses="ml-2 text-sm font-light text-kgrey-menuText mb-2 block"
									name="confirmed"
									optionLabel="title"
									optionValue="id"
									isSearchable
									isCached
									placeholder={t('select_confirmed_status')}
									reverse={true}
									isClearable
									loadOptionsUrl={`/${companyId}/my/company/case_statuses/`}
									loadOptionsParams={(search: string) => ({
										extra: {
											search,
											is_staff: !isStaff ? 'false' : 'true'
										}
									})}
									isOptionDisabled={(option: StatusType) =>
										option.id === currentSelectedStatus?.id ||
										values?.not_confirmed?.id === option.id
									}
									loadOptionsKey=""
								/>
							</div>

							<div className="mt-4 flex items-center justify-end">
								<Button variant="outlined" type="button" onClick={closeEditStatusModal}>
									{t('cancel')}
								</Button>
								<Button
									variant="contained"
									type="submit"
									className="!ml-4"
									color="primary"
									disabled={isSubmitting || !(!!values.confirmed === !!values.not_confirmed)}
								>
									{values.confirmed && values.not_confirmed ? t('add') : t('reset')}
								</Button>
							</div>
							{isSubmitting && <Loader tiled />}
						</div>
					);
				}}
			</EntityForm.Main>
		</>
	);
}
