import React from 'react';
import { DraggableProvided } from 'react-beautiful-dnd';
import { CaseItem } from './CaseItem';
import { SearchCaseItem } from './SearchCaseItem';
import { ToggleCaseDetailsOpen } from './case-details/CaseDetails.types';

type GetStyleArgs = {
	draggableStyle: any; //!
	virtualStyle: Record<string, any>;
	isDragging: boolean;
};

function getStyle({ draggableStyle, virtualStyle, isDragging }: GetStyleArgs) {
	// If you don't want any spacing between your items
	// then you could just return this.
	// I do a little bit of magic to have some nice visual space
	// between the row items
	const combined = {
		...virtualStyle,
		...draggableStyle
	};

	// Being lazy: this is defined in our css file
	const grid = 10;

	// when dragging we want to use the draggable style for placement, otherwise use the virtual style
	return {
		...combined,
		height: isDragging ? combined.height : combined.height - grid,
		// left: isDragging ? combined.left : combined.left + grid,
		// width: isDragging ? draggableStyle.width : `calc(${combined.width} - ${grid * 2}px)`,
		marginBottom: grid
	};
}

type ReactWindowItemProps = {
	provided: DraggableProvided;
	item: any;
	isDragging: boolean;
	style?: Record<string, any>;
	isFetched?: boolean;
	toggleCaseDetailsOpen: ToggleCaseDetailsOpen;
};

export function DragItem({
	provided,
	item,
	style = {},
	isDragging,
	isFetched = false,
	toggleCaseDetailsOpen
}: ReactWindowItemProps) {
	return (
		<div
			{...provided.draggableProps}
			{...provided.dragHandleProps}
			ref={provided.innerRef}
			style={getStyle({
				draggableStyle: provided.draggableProps.style,
				virtualStyle: style,
				isDragging
			})}
			className={`item ${isDragging ? 'is-dragging' : ''}`}
		>
			{'case_' in (item ?? {}) ? ( //https://youtu.be/I_jBM7MV95g
				<SearchCaseItem
					caseData={item}
					isDragging={isDragging}
					toggleCaseDetailsOpen={toggleCaseDetailsOpen}
				/>
			) : (
				<CaseItem
					caseData={item}
					isFetched={isFetched}
					isDragging={isDragging}
					toggleCaseDetailsOpen={toggleCaseDetailsOpen}
				/>
			)}
		</div>
	);
}
