/**@jsxImportSource @emotion/react */
import React, { useEffect, useState, useCallback, lazy } from 'react';
import { css } from '@emotion/react';
import { StatusHeader } from './StatusHeader';
// import { Scrollbars } from 'react-custom-scrollbars-2';
import EntityContainer from 'modules/entity/containers';
import { useAppState } from 'shared/state';
import { ErrorBoundary } from 'shared/components';
import { CaseItemLoader } from 'shared/components/Loaders';
import useDimensions from 'react-use-dimensions';
import { isMobile, isSafari } from 'react-device-detect';
import { useGetEntity, useQueryParams } from 'shared/hooks';
import { useCaseExtraParams } from '../hooks/useCaseExtraParams';
import { Draggable } from 'react-beautiful-dnd';
import { ListComponent } from './ListComponent';
import { useCaseDetailsToggle, useStatusCaseList } from '../hooks';
import { WhiteModal } from 'shared/components/Elements';
import { CaseDetails } from './case-details';
import { getCaseEntityName, sortChatList } from 'shared/services';
import clsx from 'clsx';
import { Case } from 'shared/interfaces';
import dayjs from 'dayjs';
import { useTranslation } from 'react-i18next';

export type StatusType = {
	id: number;
	title: string;
	order_number: number;
	company_id: number;
	is_staff: boolean;
	is_confirmation_required: boolean;
	is_feedback_required: boolean;
	confirmed: any;
	not_confirmed: any;
};

type Props = {
	status: StatusType;
	index: number;
};

const CaseSkeletons = () => (
	<>
		{[...Array(6)].map((item: any, idx) => {
			return (
				<div className={'mb-2'} key={idx}>
					<CaseItemLoader />
				</div>
			);
		})}
	</>
);

const NoCases = () => {
	const { t } = useTranslation();
	return (
		<div
			className={'flex-centered h-[148px] rounded-xl border border-dashed font-poppins text-kgrey'}
		>
			{t('no_cases')}
		</div>
	);
};

function StatusInner({ status, index }: Props) {
	const { query } = useQueryParams();
	const reloadCases = query?.reloadCases;
	const reloadStatusId = query?.reloadStatusId;
	const computedPage = (reloadCases === 'true' && reloadStatusId) || query?.forceUpdate ? 1 : 0;
	const [page, setPage] = useState(computedPage);
	const [searchPage, setSearchPage] = useState(computedPage);
	const { searchQuery, caseType, isPermitted, statusReverseState, isDarkMode, selectedCaseFilter } =
		useAppState();
	const [statusBodyRef, { height: statusBodyHeight }] = useDimensions();
	const { extraParams, casesUrl, selectedFilterTemplate } = useCaseExtraParams({
		statusId: status.id
	});

	const entityName = getCaseEntityName({
		statusId: status.id,
		caseType,
		searchQuery,
		selectedFilterTemplate
	});

	const { all: currentStatusCaseEntity } = useGetEntity({
		entity: 'cases',
		entityId: status.id,
		entityName
	});

	const currentStatusReverseState = statusReverseState?.[status.id as number];
	const { toggleCaseDetailsOpen, caseDetailsOpen, closeCaseDetailModal } = useCaseDetailsToggle();
	useStatusCaseList(status);

	//https://www.youtube.com/watch?v=b1_M0FJWVYQ - explanation of reloading cases implementation
	useEffect(() => {
		if (reloadStatusId && parseInt(reloadStatusId as string) === status.id) {
			setSearchPage(computedPage);
			setPage(computedPage);
		}
	}, [computedPage, reloadStatusId]);

	/* re-fetching statuses from page 1 when certain actions: changing/applying filter, reversing status
	and searching cases. "reloadStatusId" should not affect on these actions. fix case numbers (3049, 3306) */
	useEffect(() => {
		setSearchPage(computedPage);
		setPage(computedPage);
	}, [selectedFilterTemplate, currentStatusReverseState, searchQuery]);

	/*
	Complicated case fetching. Because of different endpoints
  and payload structure for case in normal and search mode
   * */

	const loadMore = useCallback(
		(hasToLoadMore) => {
			if (hasToLoadMore) {
				searchQuery.length
					? setSearchPage((prev) => (prev === 0 ? prev + 2 : prev + 1))
					: setPage((prev) => (prev === 0 ? prev + 2 : prev + 1));
			}
		},
		[searchQuery, page, searchPage]
	);

	return (
		<EntityContainer.All
			entity="cases"
			name={entityName}
			url={casesUrl}
			params={{
				extra: {
					...extraParams
				},
				limit: 10,
				page: searchQuery.length ? searchPage : page
			}}
			appendData
			cancelField="status"
			replaceIds={(page === 1 || page === 0) && !searchQuery.length}
			isCancellable={!!searchQuery.length}
		>
			{({ items, isFetched, meta }) => {
				const metaCurrentPage = meta?.current_page || 1;
				const hasToLoadMore =
					isFetched && metaCurrentPage && meta?.last_page && metaCurrentPage < meta?.last_page;

				const mappedItems = (items as Case[]).reduce((acc, curr, index) => {
					if (currentStatusReverseState) {
						//CASE 3111 - added reversed_date: number to trigger sort by this date prop when reverse status ordering is active
						return [...acc, { ...curr, reversed_date: dayjs().unix() + index }];
					} else {
						return [...acc, curr];
					}
				}, [] as Case[]);

				const sortedItems = sortChatList<Case>(mappedItems);

				return (
					<Draggable
						draggableId={status.id.toString()}
						index={index}
						isDragDisabled={isMobile || !isPermitted}
					>
						{(provided) => {
							return (
								<div
									className={clsx(
										`status status-${status.id} mr-4 flex h-full w-[362px] flex-col`,
										{
											hidden:
												isFetched &&
												!items.length &&
												(searchQuery.length || selectedCaseFilter[caseType])
										}
									)}
									{...provided.draggableProps}
									ref={provided.innerRef}
								>
									<StatusHeader
										name={status.title}
										totalCount={currentStatusCaseEntity?.meta?.count}
										statusId={status.id}
										isConfirmationRequired={status.is_confirmation_required}
										draggableHandleProps={provided.dragHandleProps}
									/>
									<div
										ref={statusBodyRef}
										className="status__body relative w-full flex-grow overflow-hidden rounded-xl bg-white dark:bg-transparent"
									>
										<div
											css={css`
												height: ${isSafari && `${statusBodyHeight}px`};
											`}
											className={clsx('status__inner h-full', !items.length && 'no-cases-found', {
												'status__inner-dark': isDarkMode
											})}
										>
											{/*<Scrollbars style={{ height: '100%' }} autoHide>*/}
											{!isFetched && items.length === 0 && <CaseSkeletons />}
											{items.length === 0 && isFetched && <NoCases />}
											<ListComponent
												status={status}
												items={sortedItems}
												isFetched={isFetched}
												loadMore={loadMore}
												hasNextPage={hasToLoadMore}
												toggleCaseDetailsOpen={toggleCaseDetailsOpen}
												resetCacheKeys={[computedPage, currentStatusReverseState]}
											/>
											{/*</Scrollbars>*/}
											<WhiteModal
												handleModalOpen={closeCaseDetailModal}
												open={Boolean(caseDetailsOpen)}
												minWidth={362}
												maxWidth={550}
												scrollable
											>
												<CaseDetails
													caseId={caseDetailsOpen}
													closeCaseDetailModal={closeCaseDetailModal}
												/>
											</WhiteModal>
										</div>
									</div>
								</div>
							);
						}}
					</Draggable>
				);
			}}
		</EntityContainer.All>
	);
}

export function Status(props: Props) {
	return (
		<ErrorBoundary>
			<StatusInner {...props} />
		</ErrorBoundary>
	);
}
