import React, { useMemo, useRef } from 'react';
import { useAppState } from 'shared/state';
import { CaseItemMarkup } from './CaseItemMarkup';
import { Priority } from 'shared/interfaces';
import { EntityDraft } from '../../GroupChat/types';
import { ErrorBoundary } from 'shared/components';
import { ToggleCaseDetailsOpen } from './case-details/CaseDetails.types';
// import { useQueryParams } from '../../../shared/hooks';

type CompanyType = {
	id: number;
	name: string;
};

export type CaseType = {
	id: number;
	title: any;
	case_number: string;
	business_company: CompanyType;
	client_company: CompanyType;
	department: {
		id: number;
		name: string;
		icon: {
			thumbnail: string | null;
			thumbnail_150: string | null;
		};
	};
	text?: string;
	sub_department: {
		id: number;
		name: string;
		icon: {
			thumbnail: string | null;
			thumbnail_150: string | null;
		};
	};
	status: {
		id: number;
		title: string;
	};
	last_message_type: string | null;
	last_message_text: string;
	last_message_author: {
		first_name: string;
		last_name: string;
	};
	last_message_time: any;
	priority: Priority;
	pin_time: null | string;
	draft_message: EntityDraft;
	assignee_ids: null | number[];
	has_linked_case: boolean;
	related_to_id: boolean;
	recurring_id: number;
	last_read_message: number | null;
};

type Props = {
	caseData: CaseType;
	isFetched: boolean;
	isDragging?: boolean;
	toggleCaseDetailsOpen: ToggleCaseDetailsOpen;
};

const CaseItemInner = (props: Props) => {
	const { caseData, isDragging, toggleCaseDetailsOpen /*, isFetched */ } = props;
	const caseRef = useRef<HTMLDivElement>(null);

	const { unreadMessagesCount /*, searchQuery, caseType*/ } = useAppState();
	// const { query } = useQueryParams<{ case: string; status: string; searchedMessageId?: string }>();

	const department = caseData.department,
		sub_department = caseData.sub_department,
		caseTitle = caseData.title,
		last_message_type = caseData.last_message_type,
		last_message_text = caseData.last_message_text,
		last_message_author = caseData.last_message_author,
		case_number = caseData.case_number,
		last_message_time = caseData.last_message_time,
		caseId = caseData.id,
		statusId = caseData?.status?.id,
		priority = caseData.priority,
		draftMessage = caseData.draft_message,
		hasLinkedCase = caseData.has_linked_case,
		relatedTo = caseData.related_to_id,
		recurringId = caseData.recurring_id,
		lastReadMessage = caseData.last_read_message,
		assigneeIds = caseData.assignee_ids;

	const hasUnread = useMemo(() => unreadMessagesCount?.[caseId], [caseId, unreadMessagesCount]);

	// const isActive = Number(query.case) === caseId;

	// useEffect(() => {
	// 	if (isActive && isFetched) {
	// 		caseRef?.current?.scrollIntoView({ behavior: 'smooth', block: 'start' });
	// 	}
	// }, [query, statusId]);

	return (
		<div ref={caseRef} className={`case-${caseId} h-full`}>
			<CaseItemMarkup
				case_number={case_number}
				caseTitle={caseTitle}
				department={department}
				sub_department={sub_department}
				last_message_author={last_message_author}
				last_message_time={last_message_time}
				last_message_type={last_message_type}
				last_message_text={last_message_text}
				hasUnread={hasUnread}
				caseId={caseId}
				statusId={statusId}
				pin_time={caseData.pin_time}
				priority={priority}
				draftMessage={draftMessage}
				assigneeIds={assigneeIds}
				isDragging={isDragging}
				toggleCaseDetailsOpen={toggleCaseDetailsOpen}
				hasLinkedCase={hasLinkedCase}
				relatedTo={relatedTo}
				recurringId={recurringId}
				lastReadMessage={lastReadMessage}
			/>
		</div>
	);
};

export function CaseItem(props: Props) {
	return (
		<ErrorBoundary isCompact>
			<CaseItemInner {...props} />
		</ErrorBoundary>
	);
}
