import React from 'react';
import { useAppState } from 'shared/state';
import { ErrorBoundary } from 'shared/components';
import { DraggableProvidedDragHandleProps } from 'react-beautiful-dnd';
import { StatusHeaderDots } from './StatusHeaderDots';
import IconButton from '@material-ui/core/IconButton';
import { IconSortDefault, IconSortDown } from '../../../shared/icons';
import clsx from 'clsx';
import { useAxios } from '../../../shared/hooks';
import { storage } from '../../../shared/services';
import { CASES_UNREAD_BADGE_COUNT } from '../constants';
import { useTranslation } from 'react-i18next';
import { useTranslationHelpers } from 'shared/hooks/useTranslationHelpers';

type Props = {
	name: string;
	totalCount: number;
	statusId: number;
	draggableHandleProps: DraggableProvidedDragHandleProps | undefined;
	isConfirmationRequired: boolean;
};

export function StatusHeaderInner({
	name,
	totalCount,
	statusId,
	draggableHandleProps,
	isConfirmationRequired
}: Props) {
	const {
		unreadCasesCountByStatus,
		selectedCaseFilter,
		caseType,
		setStatusReverseState,
		statusReverseState,
		settingsData,
		companyId,
		searchQuery
	} = useAppState();
	const { t } = useTranslation();
	const { isRTL } = useTranslationHelpers();

	const { fetchData: saveCasesReverseData } = useAxios({
		url: `/${companyId}/settings/`,
		method: 'post',
		dynamicValues: true,
		cb: {
			success: (data, args) => {},
			// eslint-disable-next-line @typescript-eslint/no-empty-function
			error: (error) => {}
		}
	});

	const handleReverseState = async () => {
		setStatusReverseState(statusId);

		const payload = {
			settings_json: {
				...settingsData?.settings_json,
				cases_reverse_data: {
					...statusReverseState,
					[statusId]: !statusReverseState?.[statusId]
				}
			}
		};

		await saveCasesReverseData({
			values: payload
		});
		if ('settings_json' in payload) {
			storage.set('casesReverseData', JSON.stringify(payload?.settings_json?.cases_reverse_data));
		}
	};

	const isFilterNotSelected = !selectedCaseFilter[caseType];
	const isThereUnreadCases =
		statusId in unreadCasesCountByStatus && unreadCasesCountByStatus[statusId] > 0;
	const isSearchEnabled = searchQuery.length > 0;
	const showUnreadCount = isThereUnreadCases && isFilterNotSelected && !isSearchEnabled;

	return (
		<div
			className={clsx(
				'status__header relative mb-4 w-full flex-shrink-0 rounded-xl bg-kgrey-bg py-3.5 pl-5 leading-none dark:bg-kdark-lighter',
				{
					'pr-14 ': !isRTL,
					'pl-14 pr-5': isRTL
				}
			)}
			{...draggableHandleProps}
		>
			<strong className={'break-words px-1.5 font-medium text-black dark:text-white'}>
				{name}
			</strong>
			<div className="inline-block">
				{totalCount > 0 ? (
					<span className={'ml-2 text-sm font-thin text-kgrey-variant-2'}>
						/ {t('total_cases')} <strong className={'font-semibold'}>{totalCount}</strong>
					</span>
				) : null}
				{showUnreadCount ? (
					<div
						className={clsx(
							'ml-2 inline-block h-6 w-6 rounded-full bg-primary text-center text-sm font-semibold leading-6 text-white',
							{
								'!w-fit px-1': unreadCasesCountByStatus[statusId] >= CASES_UNREAD_BADGE_COUNT
							}
						)}
					>
						{unreadCasesCountByStatus[statusId]}
					</div>
				) : null}
			</div>

			<IconButton
				className={clsx('!absolute  top-2', {
					'!text-primary': statusReverseState?.[statusId],
					'right-7': !isRTL,
					'left-7': isRTL
				})}
				size="small"
				onClick={handleReverseState}
			>
				{statusReverseState?.[statusId] ? <IconSortDown /> : <IconSortDefault />}
			</IconButton>
			<StatusHeaderDots
				statusId={statusId}
				name={name}
				isConfirmationRequired={isConfirmationRequired}
			/>
		</div>
	);
}

export function StatusHeader(props: Props) {
	return (
		<ErrorBoundary isCompact>
			<StatusHeaderInner {...props} />
		</ErrorBoundary>
	);
}
